:root {
    --color-outline: yellow;
}

.App {

}

header.header {
    background-color: #333;
    color: white;
    padding: 1rem;
    margin: 0;
}

.brand {
    text-transform: uppercase;
    text-shadow: 2px 2px 2px #333;
    line-height: 2em;
    font-weight: bold;
}

.brand a {
    text-decoration: none;
    color: white;
}

.brand:first-letter {
    color: red;
    font-size: 2rem;
}

.content {
    margin: 2rem 0;
    min-height: 100vh;
}

textarea#add-text,
button#add-text-btn {
    margin: 1rem 0;
    padding: 1rem;
    border: none;
    border-bottom: hotpink solid thick;
    border-radius: 4px;
}

button#add-text-btn {
    cursor: pointer;
}

button#add-text-btn:hover {
    border-bottom-color: deeppink;
    background-color: grey;
}

button#add-text-btn:active {
    border-bottom-color: red;
    background-color: lightgrey;
}

.stamps {
    margin: 2rem 0;
}

.stamps .ts {
    border-bottom: deeppink dotted 1px;
    font-size: 0.7em;
    display: inline-block;
}

.stamps .note {
    white-space: pre-wrap;
    margin: 1rem 0 3rem;
}

.stamp {
    min-height: 200px;
}

.stamp .highlighted {
    font-weight: bold;
}

footer {
    background-color: black;
    color: white;
    padding: 2rem;
}

footer a {
    color: white;
    font-weight: bold;
}

footer .controls {
    font-size: .8em;
}

footer h6 {
    margin: 0;
}

/* Extracted widths */

@media screen and (min-width: 601px) {
    textarea#add-text,
    button#add-text-btn {
        width: 30%;
        display: inline-block;
        margin: 1rem;
        vertical-align: bottom;
    }

    button#add-text-btn {
        width: 100px;
    }

    .stamps {
        margin: 1rem;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 2rem;
    }

    footer {
        width: 100%;
    }

}

@media screen and (min-width: 1201px) {
    .stamps {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }

    footer {
        display: grid;
        grid-gap: 1rem;
        grid-template-columns: 1fr 1fr 1fr;
    }

    footer .controls {
        display: grid;
        grid-gap: 1rem;
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media screen and (max-width: 600px) {
    textarea#add-text,
    button#add-text-btn {
        width: 70%;
        display: block;
        margin: 1rem;
    }

    .stamps {
        margin: 1rem;
    }

    footer {
        width: 600px;
    }
}

@media screen and (max-width: 400px) {
    textarea#add-text,
    button#add-text-btn {
        width: 80%;
    }

    footer {
        width: 100%;
        grid-template-columns: 1fr;
    }

    footer .controls {
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 300px) {
    textarea#add-text,
    button#add-text-btn {
        width: 80%;
    }

    footer {
        width: 300px;
        grid-template-columns: 1fr;
    }

    footer .controls {
        grid-template-columns: 1fr;
    }
}

.download-control,
.upload-control {
    display: block;
    padding: 1rem 3rem;
    min-width: 100px;
    border: thin solid white;
}

.tags {
    font-size: .8em;
}

.tags .tag + .tag {
    margin: 0 .5rem;
}

.tags .tag:hover {
    outline: thin solid var(--color-outline);
    cursor: pointer;
}

.filter-close {
    padding: 1rem;
    margin: 1rem;
    background-color: lightblue;
    cursor: pointer;
    display: inline-block;
}

.stamp-actions {
    visibility: hidden;
    margin: 1rem 0;
}

.stamp:hover .stamp-actions {
    visibility: visible;
}

.stamp-actions .btn {
    padding: 1rem;
    display: inline-block;
    cursor: pointer;
}